___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (max-width: 768px) {
  .restaurant-info {
    flex-direction: column; /* Ubah orientasi flex ke vertikal */
    align-items: center; /* Pusatkan item secara horizontal */
  }

  .restaurant-img {
    margin-right: 0; /* Hapus margin kanan pada gambar */
    margin-bottom: 20px; /* Tambahkan margin bawah pada gambar */
  }

  .restaurant-description {
    text-align: center; /* Pusatkan teks */
  }

  .menu-box {
    width: 100%; /* Atur lebar menu-box agar penuh pada layar kecil */
    margin-bottom: 20px; /* Tambahkan margin bawah pada menu-box */
  }
}

.close-btn {
  background-color: #ff0000; /* Warna latar belakang */
  color: #ffffff; /* Warna teks */
  border: none; /* Hapus border */
  padding: 8px 16px; /* Padding tombol yang lebih kecil */
  font-size: 14px; /* Ukuran teks yang lebih kecil */
  cursor: pointer; /* Ubah kursor menjadi tangan saat diarahkan */
  border-radius: 4px; /* Border radius untuk membuat tombol sedikit bulat */
  margin: 10px 0; /* Jarak atas dan bawah 10px */
}

.close-btn:hover {
  background-color: #cc0000; /* Warna latar belakang saat dihover */
}
