  margin: 0;
  font-family: Arial, sans-serif;
}

header {
  width: 100%;
  background-color: #10922c;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
}

.brand {
  display: flex;
  align-items: center;
}

.brand img {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

nav ul li {
  margin-right: 20px;
  margin-bottom: 10px; /* Tambahkan margin bottom */
}

nav ul li a {
  color: #fff;
  text-decoration: none;
}

.menu-toggle {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 20px;
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
  margin: 0;
}

.menu-toggle span {
  width: 100%;
  height: 2px;
  background-color: #fff;
  display: block;
}

.hero-image {
  position: relative;
  width: 100%;
  height: 450px;
  overflow: hidden;
  padding: 0 20px;
}

.hero-image img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  object-fit: cover;
  z-index: -1;
}

.hero-text {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: black;
  font-size: 36px;
  z-index: 1;
  text-align: center;
  text-shadow: -1px -1px 0 white, 1px -1px 0 white, -1px 1px 0 white, 1px 1px 0 white;
}

.hero-image p {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 1rem;
  text-align: center;
  z-index: 1;
  text-shadow: -1px -1px 0 white, 1px -1px 0 white, -1px 1px 0 white, 1px 1px 0 white;
  max-width: 80%;
  margin: 0 auto;
  line-height: 1.5;
}

@media screen and (max-width: 1200px) {
  .hero-image {
    width: 100%;
    max-width: none;
  }
  .hero-text {
    font-size: 28px;
    top: 25%;
  }
  .hero-image p {
    font-size: 0.9rem;
    bottom: 15px;
  }
}

@media screen and (max-width: 1024px) {
  .hero-image {
    height: 350px;
  }
  .hero-text {
    font-size: 28px;
    top: 25%;
  }
  .hero-image p {
    font-size: 0.9rem;
    bottom: 15px;
  }
}

@media screen and (max-width: 768px) {
  .hero-image {
    height: 250px;
  }
  .hero-text {
    font-size: 24px;
    top: 20%;
  }
  .hero-image p {
    font-size: 0.8rem;
    bottom: 10px;
  }
}

@media screen and (max-width: 480px) {
  .hero-image {
    height: 200px;
  }
  .hero-text {
    font-size: 20px;
    top: 15%;
  }
  .hero-image p {
    font-size: 0.7rem;
    bottom: 5px;
  }
}

body {
  font-family: "Poppins", sans-serif;
}

footer {
  width: 100%;
  background-color: #10922c;
  color: #fff;
  padding: 20px;
  text-align: center;
}

footer p {
  margin: 0;
  font-size: 14px;
}

@media screen and (max-width: 768px) {
  nav ul {
    display: none;
  }
  #hamburgerButton {
    /* Changed the ID to match HTML */
    display: block;
    color: #fff;
    font-size: 1.5rem;
    text-decoration: none;
  }
  #navigationDrawer {
    /* Changed the ID to match HTML */
    position: fixed;
    top: 0;
    left: 0;
    width: 200px;
    height: 100%;
    background-color: #10922c;
    transform: translateX(-100%);
    transition: transform 0.3s ease-in-out;
    z-index: 1000;
  }
  #navigationDrawer.open {
    /* Changed the ID to match HTML */
    transform: translateX(0);
  }
  #navigationDrawer ul {
    /* Changed the ID to match HTML */
    display: flex;
    flex-direction: column;
    padding: 20px;
  }
  #navigationDrawer ul li {
    /* Changed the ID to match HTML */
    margin-bottom: 10px;
  }
}

@media screen and (min-width: 769px) {
  nav ul {
    display: flex;
  }
  #hamburgerButton {
    /* Changed the ID to match HTML */
    display: none;
  }
}

nav ul li a,
.menu-toggle {
  padding: 14px 20px;
}

nav ul li {
  margin-right: 20px;
  margin-bottom: 10px; /* Tambahkan margin bottom */
}

.menu-toggle {
  margin-right: 20px;
}

.footer p {
  margin-top: 20px;
}

.skip-link {
  position: absolute;
  top: -60px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 8px;
  z-index: 100;
  text-decoration: none;
}

.skip-link:focus {
  top: 0;
}

#mainContent p {
  max-width: 800px;
  margin: 0 auto;
  font-size: 18px;
}

.detail-btn {
  background-color: #00a706;
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 10px 0;
  margin-top: auto;
  cursor: pointer;
  border-radius: 5px;
}

.detail-btn:hover {
  background-color: #45a049;
}

#rumah.restaurant {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  padding: 0 20px;
}

.restaurant-container {
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 20px;
  box-sizing: border-box;
}

.restaurant {
  display: flex;
  flex-direction: column;
}

.restaurant-img {
  width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.restaurant__name {
  font-size: 1.2em;
  margin: 0;
}

.restaurant__rating {
  margin: 5px 0;
}

.restaurant__separator {
  margin: 10px 0;
  border: 0;
  border-top: 1px solid #ddd;
}

.restaurant__place {
  margin: 0;
}

.deskripsi {
  margin: 10px 0;
}

@media only screen and (min-width: 768px) {
  #rumah.restaurant {
    grid-template-columns: repeat(3, 1fr);
  }
}

/*
  Like
*/
.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;

  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;

  font-size: 18px;
  color: white;
  cursor: pointer;
}
